*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --mainColor : #0072a0;
  --whiteColor : #fff;
  --blackColor:#000;
  --lightDark:#222;
}
nav{
  width: 100%;
  height: 70px;
  line-height: 70px;
  padding: 0 100px;
  box-shadow:  2px 2px 18px rgb(14 52 54 / 15%);
  display: flex;
  position: fixed;
  top: 0;
  background: #fff;

}
.left{
  width:50%;
  text-align: left;
}
.left a{
  font-weight: 700;
  color: var(--mainColor);
  font-size: 25px;
}
.right{
  width: 50%;
  text-align: right;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  gap: 2rem;
  width: 75
}
nav ul{
  list-style: none;
}
nav ul a {
  text-decoration: none;
}

.shoppingCart i{
  display: inline-block;
  font-size: 28px;
  color: var(--mainColor);
  /* margin-left: 25px;
  margin-bottom: 50px; */
}


/* Banner */

.container{
  width: 100%;
  padding: 0 100px;
}

header{
  width: 100%;
  background: url("../public/banner.jpg");
  height: 500px;
  margin-top: 90px;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
}
.headerText{
  background: rgba(0,0,0,.5);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.headerText h1{
  font-size: 35px;
}
.headerText{
  margin-top: 7px;
}





@media(max-width:765px){
  .headerText{
    padding: 0 15px;
  }
  .headerText h1 {
    font-size: 20px;
  }
  .products{
    flex-direction: column;
  }
  .product{
    width: 100%;
  }
}
