.inputBox
{
   display: block;
   margin: 20px;
   padding: 7px;
   width: 300px;
   border: 1px solid skyblue;
   outline: none;
}
.popup{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(189, 189, 189 , 0.9);
}
.model{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 10px;
    border-radius: 5px;
    overflow: auto;
    /* height: 600px;
    width: 50%; */
