.product{
    width: 25%;
    padding: 0 12px;
    margin: 20px 0;
    overflow: hidden;
}
.product-image{
    width: 100%;
    height: 200px;
    overflow: hidden;
}
.product-image img {
    width: 100%;
    height: 100%;
}
.product-details{
    padding: 15px 10px;
    text-align: center;
    background: var(--whiteColor);
    border-left: 1px solid #fafafa;
    border-right: 1px solid #fafafa;
}
.product-name{
    font-weight: 500;
    font-size: 18px;
    margin: 12px 0;
}
.product-price{
    font-size: 20px;
    font-weight: 300;
    margin: 12px 0;
}
.Add-to-Cart{
    background: var(--mainColor);
    padding: 10px 20px;
    margin: 0;
    width: 100%;
    color: var(--whiteColor);
    text-transform: uppercase;
    text-align: center;
    font-size: 14px;
    border: none;
    cursor: pointer;
    
}



@media(max-width:765px){
    
    .headerText{
      padding: 0 15px;
    }
    .headerText h1 {
      font-size: 20px;
    }
    .products{
      flex-direction: column;
    }
    .product{
      width: 100%;
    }
    .cartcart{
       flex-direction: column;
     
    }
    .cart-img{
        padding: 15px 0;
        /* 15px 0 */
    }
    .cart-head{
        padding: 15px 0;
        text-align: center;
    }
    .model{
      overflow: auto;
      height: 600px;
     
    }
  }
  