img{
    margin-top: 15px;
    width: 150px;
    height: 120px;
}
span{
    font-size: 20px;
    margin: 15px;
    margin-top: 50px;
    
}



button{
    height:40px;
    font-size: 18px;
    padding: 5px;
    background-color: #0072a0;
    color: #fff;
    margin-top: 43px;
    border: none;
}
.popup{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(189, 189, 189 , 0.9);
}

.model{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 10px;
    border-radius: 5px;
    overflow: auto;
    /* height: 600px;
    width: 50%; */
    /* scroll-behavior: smooth; */
}
.remove{
    width:100px; 
    background:  #0072a0; 
    border: none;
    color:white;
    
     border-radius: 10px;
     padding: 10px 5px;
}
.order{
    width:100px; 
    background: #0072a0; 
    border: none;
    color:white;
     border-radius: 10px;
     padding: 10px 5px;
    
}

