section{
    padding: 4.5rem 0 3rem;
}
:root{

	--main-color: #0072a0;
	--second-color: #192f6a;
	--text-color: #314862;
	--bg-color: #fff;

    /*Box shadow*/
	--box-shadow: 2px 2px 18px rgb(14 52 54 / 15%);
}
.footer{
	background: var(--main-color);
	color: var(--bg-color);
	border-radius: 5rem 0 0 0;

}
.footer-container{
	display: grid;
	grid-template-columns: repeat(auto-fit,minmax(240px,auto));
	gap: 2rem;
}
.footer-container h2{
	font-size: 1.7rem;
	font-weight: 500;
}
.footer-box{
	display: flex;
	flex-direction: column;
}
.footer-box h3{
	margin-bottom: 1rem;
	font-size: 1rem;
	font-weight: 400;

}
.footer-box a{
	font-size: 0.8rem;
	color: var(--bg-color);
	font-weight: 400;
	margin-bottom: 0.5rem;
}
.footer-box a:hover {
	color: var(--text-color);

}
.social a{
	font-size: 20px;
	margin-right: 1rem;
}
.social a:hover{
	color: var(--second-color);
}